import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DemoMasterCardComponent } from './master-card-view.component';

const routes: Routes = [
    { path: '', component: DemoMasterCardComponent },
    { path: '**', redirectTo: '' }
];

@NgModule({
    exports: [RouterModule],
    imports: [RouterModule.forChild(routes)]
})
export class DemoMasterCardRoutingModule {}
